  submitted: boolean = true;
  submitted1: boolean = true;
  productCode = 'CHW';
  tokenOrigin = 'Mobile';
  phoneNumber: string;
  currencyValues: any;
  currencies: any = [];
  currencyData: any;
  transactionId: any;
  nearestBrn: boolean;
  timeSlots: any;
  customPickerOptions: {};
  mydt: any;
  brnflg: any;
  accBranch: string;
  displayRadio: boolean;
  tokenCount: any;
  appointmentId: any;
  accountDetails: any;
